<template>
  <div class="login">
    <div class="login-head">
      <img src="../../../static/images/my/logo.png" alt="">
    </div>
    <div class="login-info">
      <div class="login-fill">
        <div class="login-tel">
          <input type="text" maxlength="11">
          <img src="../../../static/images/my/tel.png" alt="">
        </div>
        <div class="login-tel">
          <input type="password">
          <img src="../../../static/images/my/vercode.png" alt="">
          <span class="vercode">获取验证码</span>
        </div>
        <div class="login-tel">
          <input type="password">
          <img src="../../../static/images/my/password.png" alt="">
        </div>
      </div>
      <div class="logBtn">登陆</div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "register"
    }
</script>

<style scoped>
  .login-head {
    width: 7rem;
    height: 7rem;
    background: url("../../../static/images/my/headImg.png");
    background-size: 100% 100%;
    margin: .75rem auto 0;
    position: relative;
  }

  .login-head img {
    width: 2.8rem;
    height: 3.9rem;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -1.95rem;
    margin-left: -1.4rem;
  }

  .login-info {
    height: calc(100vh - 7rem);
    background: url("../../../static/images/my/loginbg.png");
    background-size: 100% 100%;
  }
  .login-fill{
    position: relative;
    padding-top: 2rem;
  }
  .login-fill .login-tel{
    display: block;
    width:14.5rem;
    height:2.5rem;
    position: relative;
    margin: 1.375rem auto 0;
  }
  .login-fill .login-tel:nth-child(1){

  }
  .login-fill .login-tel input{
    width: 100%;
    height: 2.5rem;
    outline: none;
    border:0;
    padding-left: 2.5rem;
  }
  .login-fill .login-tel img{
    position: absolute;
    left: 1rem;
    top: 0.6rem;
  }

  .login-fill .login-tel:nth-child(1) img{
    width: .85rem;
    height: 1.325rem
  }
  .login-fill .login-tel:nth-child(2) img{
    width: .8rem;
    height: 1.05rem
  }
  .login-fill .login-tel:nth-child(2) span{
    display: inline-block;
    position: absolute;
    right: 0;
    top: 0;
    height: 2.25rem;
    line-height: 2.25rem;
    padding-right: .5rem;
  }
  .login-fill .login-tel:nth-child(3) img{
    width: 1.175rem;
    height: 1.175rem
  }
  .isAccount{
    font-size:.7rem;
    font-family:PingFangSC-Regular;
    color: #B2B2B2;
    text-align: center;
    padding-top: 2.175rem;
  }
  .isAccount span{
    color: #F97A2E;
  }
  .logBtn{
    width: 5.25rem;
    height: 2.2rem;
    background: #F97A2E;
    color: #fff;
    font-family:PingFangSC-Regular;
    font-size: .8rem;
    margin: 1.5rem auto 0;
    border-radius: 2rem;
    text-align: center;
    line-height: 2.2rem;
  }
  .vercode{
    font-size:.7rem;
    font-family:PingFangSC-Regular;
    color: #B2B2B2;
  }
</style>



