<template>
  <div id="app">
    <div>
      <router-view/>
      <NavFoot />
      <!--    底部导航栏的空白处-->
      <div class="emty" v-show="$route.meta.footShow==undefined||$route.meta.footShow"></div>
    </div>

  </div>
</template>

<script>
  import NavFoot from "./components/common/footer";
  import HeadNav from "./components/common/header";
  export default {
    name: 'App',
    components:{
      NavFoot,
      HeadNav,
    },
    data(){
      return{
        title:this.$route.meta.title
      }
    },
    mounted() {
      console.log(this.title)
    }
  }

</script>

<style>

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  *{
    margin:0;
    padding:0;
  }
  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  .cz_img{
    width: 100%;
    height: 100%;
  }
  body{
    background: #fff;
  }
  .pd16{
    padding:0 .8rem
  }
  p{
    margin: 0;
  }
  .emty{
    height: 50px;
  }

  .mg16{
    margin: 0 .8rem;
  }
  .van-tabbar-item--active{
    color: #F97A2E ;
  }
  .van-picker__cancel{
    color: #999;
  }
  .van-tabs__line {
     height: 1px;
     background-color: #F97A2E;
   }
  .van-ellipsis{
    font-size: .8rem;
  }
  .van-tab--active {
    color: #F97A2E;
    font-size: .8rem;
  }
  .van-icon-play:before {
    color: #F97A2E;
  }
  .headerblank{
    height: 2.2rem;
  }
</style>



