<template>
    <div >
      <div class="insured mg16">
        <router-link to="/myparticipants" class="participants">
          <div class="participantsLeft">
             <span class="parLftemty">
                <img src="../../../static/images/socialsecurity/insured.png" alt="">
            </span>
            <span>参保人</span>
          </div>
          <div class="participantsRight">
            <span>请选择参保人</span>
            <img src="../../../static/images/socialsecurity/youjiantou.png" alt="">
          </div>
        </router-link>
        <div class="insuredLine"></div>
        <div class="insuredCity">
          <div class="insuredCityInfo" @click="isCity">
            <div class="participantsLeft">
              <span class="parLftemty">
                <img src="../../../static/images/socialsecurity/localtion.png" alt="">
              </span>
              <span>参保城市</span>
            </div>
            <div class="participantsRight">
              <span class="addInfo">{{addrInfo}}</span>
              <img src="../../../static/images/socialsecurity/youjiantou.png" alt="">
            </div>
          </div>
          <p>最早可办理<em>8月份</em></p>
        </div>
      </div>
      <van-popup
        v-model="show"
        position="bottom"
        :style="{ height: '20%' }">
        <van-popup v-model="show"  position="bottom" :overlay="true">
          <van-picker show-toolbar @cancel="cancel" @confirm="confirm" title="参保月数" :columns="columns" />
        </van-popup>
      </van-popup>

    </div>
</template>

<script>
  const citys = {
    '浙江': ['杭州', '宁波', '温州', '嘉兴', '湖州'],
    '福建': ['福州', '厦门', '莆田', '三明', '泉州']
  };
    export default {
      name: "participants",
      data(){
          return{
            areaList:{
              province_list: {
                110000: '北京市',
                120000: '天津市'
              },
              city_list: {
                110100: '北京市',
                110200: '县',
                120100: '天津市',
                120200: '县'
              },
            },
            show:false,
            addrInfo:"请选择参保城市",
            columns: [
              {
                values: Object.keys(citys),
                className: 'column1'
              },
              {
                values: citys['浙江'],
                className: 'column2',
                defaultIndex: 2
              }
            ],
          }
      },
      methods:{
        getCity(){
          this.$ajax.post(this.HOST+ '/app/index/getCity')
            .then((res)=>{
              let city = res.data.data;
            })

        },
        isCity(){
          this.show = true
        },
        cancel(){
          this.show = false
        },
        confirm(e){
          this.addrInfo = e[0] + " " +e[1]
          this.show = false
        },
        onChange(){

        }



      },
      mounted() {
        this.getCity()
      }
    }
</script>

<style scoped>
  .insured{
    width: calc(100% - 1.6rem);
    height: 6.875rem;
    background: #fff;
    box-shadow:0px 0px 7px 0px rgba(0, 0, 0, 0.13);
    border-radius:.3rem;
  }
  .parLftemty{
    display: inline-block;
    width: 1.7rem;
  }
  .participants{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 2.975rem;
    padding:0 .8rem;

  }
  .participants .participantsLeft img{
    width:1.05rem;
    height: 1rem;
    vertical-align: middle;
  }
  .participants .participantsLeft span{
     width:1.05rem;
     height: 1rem;
    font-size:.7rem;
    font-family:PingFangSC-Regular;
    color: #333333;
    vertical-align: middle;
   }
  .participants .participantsRight img{
    width:.375rem;
    height: .65rem;
    vertical-align: middle;
  }
  .insuredLine{
    height: 1px;
    width: calc(100% - .8rem);
    margin-left: .8rem;
    background: #E6E6E6;
  }
  .participants .participantsRight span{
    width:1.05rem;
    height: 1rem;
    font-size:.6rem;
    font-family:PingFangSC-Regular;
    color: #999999;
    vertical-align: middle;
  }

  .insuredCityInfo{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 2.475rem;
  }
  .insuredCityInfo .participantsLeft img{
    width:.725rem;
    height: 1.075rem;
    vertical-align: middle;
  }
  .insuredCityInfo .participantsLeft span{
    width:1.05rem;
    height: 1rem;
    font-size:.7rem;
    font-family:PingFangSC-Regular;
    color: #333333;
    vertical-align: middle;
  }

  .insuredCityInfo .participantsRight img{
    width:.375rem;
    height: .65rem;
    vertical-align: middle;
  }
  .insuredCityInfo .participantsRight span{
    width:1.05rem;
    height: 1rem;
    font-family:PingFangSC-Regular;
    color: #333333;
    font-size: .7rem;
    vertical-align: middle;
  }
  .insuredCity{
    padding:0 .8rem;
  }

  .insuredCity p{
    color: #B2B2B2;
    font-size: .6rem;
  }
  .insuredCity p em{
    font-style: normal;
    color: #F97A2E;
  }
  .cityfoot{
    position: fixed;
    bottom: 0;
    width: 100%;
  }
  .addInfo{
    color: #333333;
    font-size: .7rem;
  }
</style>



