import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Socialsecurity from '@/components/socialsecurity'
import Calculator from '@/components/calculator'
import My from '@/components/my'
import Interdetail from '@/components/interpretation/interdetail'
import Myparticipants from '@/components/myparticipants/index'
import Login from '@/components/my/login'
import Register from '@/components/my/register'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '/',
      component: Index,
      meta: {
        footShow: true,
        title:"文章详情页",
        isTitle:false,

      }
    },
    {
      path: '/socialsecurity',
      name: 'socialsecurity',
      component: Socialsecurity,
      meta: {
        footShow: true,
        title:"缴社保",
        isTitle:true,
      }
    },
    {
      path: '/calculator',
      name: 'calculator',
      component: Calculator,
      meta: {
        footShow: true,
        title:"计算器",
        isTitle:true,
      }
    },
    {
      path: '/my',
      name: 'my',
      component: My,
      meta: {
        footShow: true,
        title:"我的",
        isTitle:false,
      }
    },
    {
      path: '/interdetai',
      name: 'interdetail',
      component: Interdetail,
      meta: {
        footShow: false,
        title:"文章详情页",
        isTitle:true,
      }
    },
    {
      path: '/myparticipants',
      name: 'myparticipants',
      component: Myparticipants,
      meta: {
        footShow: false,
        title:"我的参保人",
        isTitle:true,
        isRight:false,
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        footShow: false,
        title: "我的登录",
        isTitle: false,
        isRight: false,
      },
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta: {
        footShow: false,
        title:"我的注册",
        isTitle:false,
        isRight:false,
      }
    }

  ]
})



